<template>
  <div class="collection-page">
    <div class="video-card-container" v-if="Object.keys(collectionData).length>0">
      <VideoCard
        v-for="(data) in Object.values(collectionData)"
        :key="data.id"
        :id="data.id"
        :videoData="data"
        :isCollected="collectionIds[data.id]"
      />
    </div>
  </div>
</template>

<script>
import VideoCard from "@/components/VideoCard.vue";

import { mapGetters } from "vuex";
export default {
  name: "Collection",
  components: { VideoCard },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["collectionIds", "collectionData"]),
  },
  watch: {},
  mounted() {},
};
</script>


<style lang="scss" scoped>
.collection-page {
  padding: var(--top-nav-height) 10px 0;
}
.video-card-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 0px 10px;
}
</style>
