<template>
  <div>
    <button class="button" title="第一頁" @click="goToPage(from)">{{this.from}}</button>
    <button class="button" title="上一頁" @click="goToPage(current-1)">&#8249;</button>
    <button
      :class="{button:true,active:current===val}"
      :title="`第${val}頁`"
      v-for="(val) in currentGroup"
      :key="val"
      @click="goToPage(val)"
    >{{val}}</button>
    <button class="button" title="下一頁" @click="goToPage(current+1)">&#8250;</button>
    <button class="button" title="最末頁" @click="goToPage(to)">{{this.to}}</button>
  </div>
</template>

<script>
import { range } from "@/utils";
/**
 * @current 目前頁數
 * @from 起始頁
 * @to 最末頁
 * @displayCount 要顯示的按鈕數
 */
export default {
  name: "Pagination",
  props: {
    current: { type: Number, default: 1 },
    from: { type: Number, default: 1 },
    to: { type: Number, default: 1 },
    displayCount: { type: Number, default: 1 },
  },
  data() {
    return {};
  },
  methods: {
    goToPage(id) {
      if (id <= this.to && id >= this.from) {
        this.$emit("update:pageindex", id);
      }
    },
  },
  computed: {
    totalPageCount() {
      return this.to - this.from + 1;
    },
    validDisplayCount() {
      // 驗證使用者要展示的頁數有無大於總頁數
      if (this.displayCount > this.totalPageCount) {
        return this.totalPageCount;
      } else {
        return this.displayCount;
      }
    },
    currentGroup() {
      // 離第一頁或最後一頁的安全距離
      const margin = Math.floor(this.validDisplayCount / 2);
      if (this.current + margin > this.to) {
        const start = this.to + 1 - this.validDisplayCount;
        const end = this.to + 1;
        return range(start, end);
      } else if (this.current - margin < this.from) {
        const start = this.from;
        const end = this.from + this.validDisplayCount;
        return range(start, end);
      } else {
        const start = this.current - margin;
        const end = this.current + margin + 1;
        return range(start, end);
      }
    },
  },
  watch: {},
  mounted() {},
};
</script>


