@use '@/scss/mixin.scss' as *;
.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 1.5rem;
  height: 1.5rem;
  margin: 5px;
  cursor: pointer;
  border-radius: 3px;
  outline: none;
  border: 1px solid var(--black-1);
  user-select: none;
  &.active {
    background-color: var(--button-prime);
    color: var(--white-1);
  }
  @include respond-to("sm") {
    width: 2rem;
    height: 2rem;
  }
}
</style>
