function makeQueryString(obj) {
  let res = ""
  for (let [key, val] of Object.entries(obj)) {
    res += `&${key}=${val}`
  }
  return res.substring(1)
}
const BaseURL = 'https://www.googleapis.com/youtube/v3/videos'
const BaseParams = {
  part: 'snippet,contentDetails,player',
  chart: 'mostPopular',
  regionCode: 'tw',
  maxResults: 50,
  key: 'AIzaSyAaCFYIjQLAQwXC_uRNWm4_Np3u0byd350'
}
export async function fetchYtData(params) {
  const _params = Object.assign({}, BaseParams, params)
  const URL = BaseURL + '?' + makeQueryString(_params)
  const res = await fetch(URL)
  const json = await res.json()
  return json
}

export default { fetchYtData }