        "description": "【欢迎订阅影视剧透社 Subscribe to Drama Crush：https://bit.ly/3cZNCtJ 】 \n▹《以家人之名》更多预告&花絮 | Clips:http://suo.im/60OyNf\n▹《以家人之名》更多精彩片段 | More Highlights:http://suo.im/5w647U\n▹《以家人之名》全集 | Go Ahead Full Playlist:https://bit.ly/2PsWJcD\n\n▹剧集信息：\n导演: 丁梓光\n编剧: 水阡墨 / 王雄成\n主演: #谭松韵 / #宋威龙 / #张新成 / 孙铱 / 何瑞贤 / 安戈\n类型: 剧情 / 家庭\n制片国家/地区: 中国大陆\n语言: 汉语普通话\n首播: 2020-08-10(中国大陆)\n集数: 40\n\n▹剧情简介：\n    　因为家庭变故，三个没有血缘关系的小孩成为了彼此新的家人。青梅竹马一起长大的三兄妹，一同经历，相互扶持，不因来时坎坷而沮丧，也不因前路漫漫而退缩。然而，原生家庭造成的心理问题如影随形，他们谒望被爱，却更加害怕失去。而曾经伤害过他们的人也再次闯入，打着家人的旗号想把他们分开。 \n　　一边是彼此认定相互珍惜的新家人，一边是无法选择关系谈漠的亲生父母，他们艰难抉择却还是顾此失彼。他们不想新家人受到伤害，也不想人生被拖向深渊。 \n　　面对两难的境遇，他们勇敢面对， 也努力让父母直面了自己的问题。在不同关系的爱中， 他们教会了自己成长，明晰了自己的内心一唯有直面原生家庭的影响，以家人之名相互治愈，才能清醒无畏的走下去， 成为更好的自己。",
