        "channelId": "UCKQVSNdzGBJSXaUmS4TOWww",
        "title": "嚇誰呀？「8月打、9月收、喜迎十一」解放軍嗆台海軍演 中國網友High翻！【關鍵時刻】20200814-1劉寶傑 李正皓 黃世聰 王瑞德 姚惠珍",
        "description": "◆加入關鍵時刻會員【關鍵時刻網路專家】→https：//bit.ly/31smots\n\n嚇誰呀？「8月打、9月收、喜迎十一」解放軍嗆台海軍演 中國網友High翻！\n解放軍宣布軍演「甕中捉鱉」陸海空全上陣？ 從台海兩端縮到內海…！？\n喊哪堵哪？雷根號「超前部署」盯住解放軍軍演 第七艦隊潛艇部隊曝光！\n全美僅三架RC–135S急奔沖繩嘉守納基地 解放軍飛彈出動全程監控！？ \n不畏戰！嘉義基地F–16編隊飛彈「掛好掛滿」 反制匿蹤戰機行頭亮相！？  \n台灣洽購「新型迅擊」智慧水雷反登陸 解放軍報嗆「登島」沒這麼簡單！\n解放軍環球時報引用馬英九「首戰即終戰」 李正皓：前總統長他人志氣？\n北戴河會議氣氛異常詭譎 習近平定調「三軟三硬」向美釋求和信號！？\n中南海每家人都避不見面… 北戴河會議「異常靜默」背後是腥風血雨！\n紐時爆料精準砲打習核心 獨漏二當家李克強背後…中南海內鬥再起？\n習江狠鬥紀曉波傳破產 朋友圈還有「三個100」賴小民收賄75億台幣！ \n「我幫你擋，你給克強多一點責任」 胡錦濤一句話「習李體制」陷分裂！？\n習近平打貪雷厲風行家族海外資產超龐大 江派手中最後核彈是「她」？   \nF–16掛滿飛彈編隊升空… AIM–120空對空飛彈獵殺率達85%威力驚人！\n中官媒總編嗆「飛越台灣」 王瑞德：直奔陰曹地府，台灣射控雷達嚇人！\n解放軍劍指台海實戰化演練放話警告 王瑞德：台灣不求戰也不懼戰！\n美軍考慮部署「超級巨砲基地」 前進東亞可打北京、上海或整個華東！？  \n賀錦麗出線拜登副手「24小時募款超過7億」 川普「最大訂單」沒效了？\n副手賀錦麗砲火猛幫拜登加分？ 川普勃然大怒總統之尊罵「瘋女人」！\n拜登副手讓矽谷科技、金融業鬆一口氣？ 賀錦麗恐讓美大選更加慘烈！\n\n《關鍵時刻》粉絲團：https://www.facebook.com/CTimefans\n《關鍵時刻》頻道訂閱：https://bit.ly/2OlcnV7\n\n#劉寶傑 #關鍵時刻 #解放軍#軍演#中國#台灣#雷根號#第七艦隊#潛艇#美國#飛彈#馬英九#北戴河#習近平",
