        "title": "Finally, a different title! “The life of okra and bamboo fence, LOL这次可算有点不一样的标题了！秋葵，和竹筒路的一生嘻嘻丨Liziqi",
        "description": "Please  subscribe to 【李子柒 Liziqi 】Liziqi Channel on YouTube if you like my videos: https://goo.gl/nkjpSx \n“一棵花藤攀着高低木栅栏蔓延生长，朝着光”\n这是几年前在一部动画片里所记住的画面…\n今年春天，我也种下类似这样一条竹筒花路\n旁边还跟着种了一片秋葵！\n春去秋来，无数藤蔓和果实都在向阳而生，\n总归是充满希望的吧！\n“A climbing vine wires up and down the wood fence, following faithfully the path of light.”\nThat is the scene I could remember from a cartoon film a few years ago…\nThis spring, I built a similar bamboo fence of flowering vines,\nAnd planted a patch of okra next to it!\nWhen spring fades into autumn glory, a multitude of vines and fruits turn their faces toward the sun.\nFull of hopes, as always.\n\n#李子柒#李子柒Liziqi #Chinese Cuisine #Chinese Food\n\n\n 【李子柒 Liziqi 】 https://goo.gl/nkjpSx\n--------------------------------------------------------------------\nliziqi李子柒更多精彩视频推荐/More wonderful video recommendations：\nliziqi李子柒精选视频/decent video selection：https://is.gd/qe9xuc\n李子柒●花开有声 | Soud of blooming flowers： https://is.gd/tyr1ja\n李子柒 ●饮食以节 | Chinese festival food： https://is.gd/3LsFnu\n李子柒 ●适时而食 | Seasonal diet： https://is.gd/JmLJH3\n东方非遗传承 | Oriental intangible cultural heritage ： https://is.gd/atatyo\n传统工艺 | Traditional handicraft： https://is.gd/FPl4df\nSpring 春之卷：https://is.gd/EBwo2n\nSummer 夏之卷：https://is.gd/i1jJ46\nAutumn 秋之卷：https://is.gd/YBVeL2\nWinter 冬之卷：https://is.gd/3JqW8D\n\n#LýTửThất #李子柒 #liziqi",
