          "description": "劇情重點：\n慧君接到羅俊電話，告知三泰說要去當年志龍墜崖處重新開始，眾人急忙趕去。三泰抱著如萍站在懸崖邊，對如萍說抱歉，若不是走投無路了，他也不想這樣做，志龍等人趕到，勸三泰回頭別做傻事，三泰向慧君和成皓道歉，要成皓好好照顧慧君，慧君要三泰別想不開，三泰轉向志龍，表示最不想輸給志龍，但最終還是輸了，說著自己的不甘願，三泰想到將和如萍訣別，心疼難受的親吻如萍的額頭後，抱著如萍往懸崖走去，眾人急要三泰別做傻事，突然三泰停步轉身，對眾人表示曾想帶著如萍一起死，但實在捨不得如萍就此死去，所以決定將如萍還給志龍，要志龍上前抱走如萍，眾人震驚錯愕，志龍難以相信，小心翼翼上前，沒想三泰竟真的將如萍交給志龍，志龍發現如萍情況危急，趕緊抱著如萍離去就醫，慧君往三泰走去緊緊抱住三泰，要陪三泰一起離去，三泰感動，對慧君表示歉意，就在兩人要跳崖時，三泰突然將慧君推回，不讓慧君隨自己跳下....\n\n志龍抱著如萍狂奔，替重傷的如萍打氣，表示救護車已在來路，一定要撐住，如萍回想著和志龍的過往，最後只能無力擠出謝謝志龍愛過她，一行淚從眼角滑落，雙眼一閉，手無力的滑下，志龍大喊如萍……\n\n小飛好奇追問玉蓮和世昌到底發生何事，怎會鬧分居，玉蓮要她別管，員工來告知有奧客鬧場，兩人趕緊前往處理，正危急時，加烈趕到，以律師專業替小飛解圍，加烈問玉蓮可否和小飛去參加同學會，玉蓮擔心，加烈偷偷告知已通知同學不能提起正群事，玉蓮才同意。同學有人問起兩人怎會一起出席，是否要復合，小飛才驚覺原來自己真的和加烈交往過，好奇兩人分手原因，加烈不好說，只表示是自己對不起小飛，小飛猜測是否加烈劈腿，加烈推說玉蓮不讓講過去事，要小飛饒了他別再追問了，此時有同事奚落加烈從律師變成賣點心的，小飛跳出來護加烈幫他解圍，加烈才發現自己對小飛的愛從未停歇…\n\n順豐熱著滴雞精，病床上如萍緩緩醒來，原來如萍沒事了，如萍看著順豐，激動萬分，兩人想到過去二十多年的噩夢恍如隔世，不禁相擁而泣，但也慶幸可以重新生活，志龍來，順豐感謝志龍真的救回如萍，三人慶幸三泰伏法，大家終於可以回到原本的生活了，如萍詢問志龍應該很快要回美國了吧，志龍知道兩人終是無法再回到過去...\n\n加烈送喝醉的小飛回家，玉蓮很緊張，加烈表示同學沒說溜嘴，玉蓮才稍稍放心，警告加烈別以為有機會可以和小飛交往，小飛奇怪玉蓮為何如此討厭加烈，玉蓮含糊帶過。\n\n三泰擔心如萍安危，獄警通知有人面會，三泰以為是如萍，沒想竟是順豐，三泰急問如萍情況，順豐要他放心，三泰奇怪順豐為何會來看他，順豐拿出離婚協議書，上面已簽好如萍的名字，三泰大驚，不肯簽名，順豐開導三泰，如是真的愛如萍，為了如萍好，就要放手，三泰終是含淚簽名。順豐拿協議書給如萍，如萍謝謝順豐，欣慰一切終於過去了…\n\n🤩 民視優質戲劇搶先看 🤩\n🔔免費訂閱【民視戲劇館】👉🏻 https://reurl.cc/NE1O9\n\n🎁 更多精彩官方影片，請關注我們 🎁\n四季線上影視FB👉🏻 https://goo.gl/xzMmw3\n民視娛樂FB👉🏻 https://goo.gl/EFflxq\n\n📺 四季線上影視4gTV 免費戲劇新上架 📺\n四季線上影視4gTV：https://4gtv.tv\nAPP下載：https://reurl.cc/zyW56y"
