import NavBar from "@/view/NavBar.vue";
import LoadingIcon from "@/svg/Loading.vue";
import VideoContainer from "@/components/VideoContainer.vue";
import { mapGetters } from "vuex";
export default {
  name: "App",
  components: { NavBar, VideoContainer, LoadingIcon },
  computed: {
    ...mapGetters(["playerEmbedHtml", "isLoading"]),
  },
  mounted() {
    if (process.env.NODE_ENV === "production") {
      this.$store.dispatch("fetchYtData");
    }
  },
};
</script>

