<template>
  <div class="video-card">
    <div class="yt-thumbnail-container">
      <img class="yt-thumbnail" :src="thumbnail" @click="openPlayer" alt />
      <div class="yt-duration">{{duration}}</div>
      <button
        :class="{'collection-button':true,active:isCollected}"
        @click="toggleCollection"
      >{{isCollected?`&#9733;已收藏`:`&#9734;收藏`}}</button>
    </div>
    <div class="yt-content">
      <h3 class="yt-title">{{title}}</h3>
      <p class="yt-description">{{description}}</p>
    </div>
  </div>
</template>

<script>
import { formatYtDuration } from "@/utils";
export default {
  name: "VideoCard",
  props: {
    id: String,
    isCollected: [Number, Boolean],
    videoData: Object,
  },
  data() {
    return {};
  },
  computed: {
    duration() {
      return formatYtDuration(this.videoData.contentDetails.duration);
    },
    title() {
      return this.videoData.snippet.title;
    },
    description() {
      return this.videoData.snippet.description;
    },
    thumbnail() {
      return this.videoData.snippet.thumbnails.standard.url;
    },
    playerEmbedHtml() {
      return this.videoData.player.embedHtml;
    },
  },
  watch: {},
  methods: {
    toggleCollection() {
      if (this.isCollected) {
        this.$store.commit("removeFromCollection", { id: this.id });
      } else {
        const timestamp = Date.now();
        this.$store.commit("addToCollection", { id: this.id, timestamp });
      }
    },
    openPlayer() {
      this.$store.commit("openPlayer", this.playerEmbedHtml);
    },
  },
  mounted() {},
};
</script>


