  name: "NavBar",
  components: { Hamburger },
  data() {
    return {
      routes,
      isMenuOpen: false,
    };
  },
  methods: {
    toggleMenu() {
      this.isMenuOpen = !this.isMenuOpen;
    },
  },
  computed: {
    currentRouteName() {
      return this.$route.name;
    },
  },
  watch: {},
  mounted() {},
};
