import Vue from 'vue'
import Vuex from 'vuex'
import API from '@/ytAPI'
import { mockData } from '@/mockData'
Vue.use(Vuex);
const defaultYtData = process.env.NODE_ENV === 'production' ? {} : { ...mockData }
export const store = new Vuex.Store({
  state: {
    isLoading: false,
    ytData: defaultYtData,
    collectionIds: JSON.parse(localStorage.getItem('collectionIds')) ?? {},
    playerEmbedHtml: null
  },
  mutations: {
    openPlayer(state, embedHtml) {
      state.playerEmbedHtml = null
      state.playerEmbedHtml = embedHtml
    },
    closePlayer(state) {
      state.playerEmbedHtml = null
    },
    setLoading(state, isLoading) {
      state.isLoading = isLoading
    },
    setYtData(state, payload) {
      state.ytData = payload
    },
    addToCollection(state, payload) {
      const { id, timestamp } = payload
      state.collectionIds = { ...state.collectionIds, [id]: timestamp }
      localStorage.setItem('collectionIds', JSON.stringify(state.collectionIds))
    },
    removeFromCollection(state, payload) {
      const { id } = payload
      const { [id]: removed, ...rest } = state.collectionIds
      state.collectionIds = rest
      localStorage.setItem('collectionIds', JSON.stringify(state.collectionIds))
    },
  },
  actions: {
    async fetchYtData(context, _) {
      context.commit('setLoading', true)
      const res = await API.fetchYtData()
      const nextPageToken = res.nextPageToken
      const nextPageRes = await API.fetchYtData({ pageToken: nextPageToken })
      const fullRes = [...res.items, ...nextPageRes.items]
      const payload = { ...fullRes }
      context.commit('setYtData', payload)
      context.commit('setLoading', false)
    }
  },
  getters: {
    isLoading: (state) => state.isLoading,
    ytData: (state) => state.ytData,
    collectionIds: (state) => state.collectionIds,
    collectionData: (state) => {
      const collectionData = Object.values(state.ytData).filter(each => state.collectionIds[each.id])
      return collectionData
    },
    playerEmbedHtml: (state) => state.playerEmbedHtml
  }

})