export function formatYtDuration(input) {
  const minutePattern = /[0-9]{1,2}M/;
  const secondPattern = /[0-9]{1,2}S/;
  const hourPattern = /[0-9]{1,2}H/;
  const [hour] = input.match(hourPattern) ?? ["00H"];
  const [minute] = input.match(minutePattern) ?? ["00M"];
  const [second] = input.match(secondPattern) ?? ["00S"];
  let hh = hour.slice(0, -1);
  let mm = minute.slice(0, -1);
  let ss = second.slice(0, -1);
  hh = hh.length < 2 ? `0${hh}` : hh
  mm = mm.length < 2 ? `0${mm}` : mm
  ss = ss.length < 2 ? `0${ss}` : ss
  const res = hh > 0 ? `${hh}:${mm}:${ss}` : `${mm}:${ss}`;
  return res;
}

export function range(start, end) {
  const length = end - start;
  const res = [...new Array(length).keys()].map((each) => each + start);
  return res;
}

export function createResponsiveIframe(embedHtml) {
  const container = document.createElement("div");
  container.innerHTML = embedHtml;
  container.style.cssText = `position: relative;
                             overflow: hidden;
                             width: 100%;
                             padding-top: 56.25%;`;
  const iframe = container.childNodes[0];
  iframe.width = "100%";
  iframe.height = "100%";
  iframe.style.cssText = `position: absolute;
                          top: 0;
                          left: 0;
                          bottom: 0;
                          right: 0;
                          width: 100%;
                          height: 100%;`;
  return container;
}