<template>
  <div :class="{'background':true,'at-corner':atCorner}">
    <div class="modal">
      <div class="iframe-container" ref="iframeContainer"></div>
      <div class="button-group">
        <button
          class="button"
          :title="atCorner?`放大`:`縮小`"
          @click="toggleToCorner"
        >{{atCorner?`&#10138;`:`&#10136;`}}</button>
        <button class="button" title="關閉" @click="handleClose">&#10006;</button>
      </div>
    </div>
  </div>
</template>

<script>
import { createResponsiveIframe } from "@/utils";
export default {
  name: "VideoContainer",
  props: {
    embedHtml: String,
  },
  data() {
    return { atCorner: false };
  },

  methods: {
    handleClose() {
      this.$store.commit("closePlayer");
    },
    toggleToCorner() {
      this.atCorner = !this.atCorner;
    },
    replaceIframe() {
      const res = createResponsiveIframe(this.embedHtml);
      this.$refs.iframeContainer.innerHTML = "";
      this.$refs.iframeContainer.append(res);
    },
  },
  mounted() {
    this.replaceIframe();
  },
  watch: {
    embedHtml() {
      this.replaceIframe();
    },
  },
};
</script>


