<template>
  <div class="home-page">
    <header class="page-title">The Most Popular Videos on Youtube Now 🔥🔥</header>
    <div class="video-card-container" v-if="Object.keys(currentPageData).length>0">
      <VideoCard
        v-for="(data) in currentPageData"
        :key="data.id"
        :id="data.id"
        :videoData="data"
        :isCollected="collectionIds[data.id]"
      />
    </div>

    <div class="pagination-container">
      <Pagination
        v-if="pageCount>1"
        :from="1"
        :to="pageCount"
        :current="current"
        :displayCount="5"
        @update:pageindex="goToPage"
      />
    </div>
  </div>
</template>

<script>
import VideoCard from "@/components/VideoCard.vue";
import Pagination from "@/components/Pagination.vue";

import { mapGetters } from "vuex";
import { range } from "@/utils";
export default {
  name: "Home",
  components: { VideoCard, Pagination },
  data() {
    return {
      current: 1,
      maxResult: 12,
    };
  },
  computed: {
    ...mapGetters(["ytData", "collectionIds", "collectionData"]),
    currentPageData() {
      const indexes = range((this.current - 1) * 12, this.current * 12);
      const pageData = indexes.map((id) => this.ytData[id]).filter((v) => v);
      return pageData;
    },
    pageCount() {
      const totalDataCount = Object.keys(this.ytData).length;
      const res = Math.ceil(totalDataCount / this.maxResult);
      return res > 1 ? res : 1;
    },
  },
  mounted() {},
  methods: {
    goToPage(id) {
      this.current = id;
    },
  },
};
</script>

<style lang="scss" scoped>
.home-page {
  padding: var(--top-nav-height) 10px 0;
}
.page-title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  line-height: 2rem;
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 2px;
  width: 100%;
  margin: 10px auto;
  color: var(--white-1);
}

.video-card-container {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  grid-gap: 0px 10px;
}
.pagination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3rem;
  position: sticky;
  bottom: 0;
}
</style>
